
import {
    font_path,
    font_src,

    calc_list_width,
    calc_list_height,

    push,
    edit,
} from './f/i.js';

import {
    window_event_object,
    passive_false,
    messages_fragment,
    messages_range,
    font_faces,
    workers,
    TD,
    TE,
} from "./conf/i.js";

import {
    html_style,
    body_cl,
    document,
    window,
    
    list,
    
    scrollbar_x,
    scrollbar_y,

    elements,

    MESSAGE_ROW_EL,
} from "./elems/i.js";

import {
    style_state,
    support_font_format,
    support_font,
    alloc_state,
    mode_state,
    language_state,
} from './state/i.js';

import {
    on_window_resize,
    on_contextmenu,
    on_error,
    on_list_mousedown,
    on_list_selectstart,

    on_scrollbar_thumb_mousedown,
    on_list_wheel,

    on_window_keydown,
    on_window_keyup,
} from './on/i.js';

(
    (window, document, Math, FontFace) => {
        var
            document_fonts = document.fonts,

            row_height = style_state.row_height,
            font_id = style_state.font_id,

            font_id_bare_str = (`_${font_id}`),

            font = support_font[font_id],
            font_name = font.name,
            font_face = null,

            list_width = 0,

            width = window.innerWidth,
            height = window.innerHeight,
            
            list_left = style_state.list_left,
            list_right = style_state.list_right,

            list_top = style_state.list_top,
            list_bottom = style_state.list_bottom
        ;

        workers[0] = new Worker("/f/text/javascript/worker/fs/1.js");
        
        window.onerror = on_error;
        window.contextmenu = on_contextmenu;
        window.onresize = on_window_resize;
        list.onmousedown = on_list_mousedown;
        list.onselectstart = on_list_selectstart;

        window.addEventListener("wheel", on_list_wheel, passive_false);
        window.onkeydown = (on_window_keydown);
        window.onkeyup = (on_window_keyup);

        scrollbar_y.onmousedown = on_scrollbar_thumb_mousedown;

        (((mode_state).row_width) === 0)
        ? (
            scrollbar_x.onmousedown = on_scrollbar_thumb_mousedown
        )
        : (
            scrollbar_x.classList.add('none')
        );
        
        html_style.setProperty("--row-height", `${row_height}px`);
        html_style.setProperty("--font-size", `${style_state.font_size}px`);
        html_style.setProperty("--font-family",`"${font_id_bare_str}"`);

        html_style.setProperty("--list-top", `${list_top}px`);
        html_style.setProperty("--list-left", `${list_left}px`);
        html_style.setProperty("--list-right", `${list_right}px`);
        html_style.setProperty("--list-bottom", `${list_bottom}px`);

        html_style.setProperty("--background-color", '#FFFFFFFF');
        html_style.setProperty("--color", "#000000FF");
        html_style.setProperty("--placeholder-color", '#808080FF');
        
        document_fonts.add(
            (
                font_face = (
                    new FontFace(
                        font_id_bare_str,
                        font_src(
                            support_font_format,
                            font_id,
                            font_name,
                            0,
                            support_font_format.length,
                            font_path,
                        ),
                        style_state
                    )
                )
            )
        );

        return (
            (
                font_faces[font_id] = font_face
            )
            .load()
            .then(
                () => {
                    var
                        placeholder_value = language_state.placeholder_value,

                        TEXT = 0,
                        ENCODING = 0,
                        UTF8 = 0,
                        
                        push_text_utf8 = edit[TEXT][ENCODING][UTF8],

                        input = {
                            "blocks": [
                              "Hello привіт\nSecond msg: test тест\nComplaint: blocks field only? please keep messages meta!\nAnother content: Kyiv/Київ, coffee кава.\nBye па-па\n"
                            ],
                            "messages": [
                              { "chat_id": 0, "id": 0, "block_id": 0, "offset": 0,   "length": 13, "timestamp": 0 },
                              { "chat_id": 0, "id": 1, "block_id": 0, "offset": 13,  "length": 22, "timestamp": 1 },
                              { "chat_id": 0, "id": 2, "block_id": 0, "offset": 35,  "length": 57, "timestamp": 2 },
                              { "chat_id": 0, "id": 3, "block_id": 0, "offset": 92,  "length": 41, "timestamp": 3 },
                              { "chat_id": 0, "id": 4, "block_id": 0, "offset": 133, "length": 10, "timestamp": 4 }
                            ]
                        },

                        blocks = [
                            TE.encode(input.blocks[0])
                        ],

                        messages = input.messages,


                        render_element = (
                            (
                                element,
                                string,
    
                                offset,
                                to,
                            ) => {
                                return (
                                    element
                                    .firstElementChild
                                    .textContent = (
                                        string.substring(offset, to)
                                    )
                                );
                            }
                        ),


                        expand_nodes = (
                            (
                                elems,
                                i,
                                l,
                                template,
                                alloc_state,
                            ) => {
                                var
                                    size_elements = alloc_state.size_elements,

                                    new_length = (l + size_elements)
                                ;
                                elems.length = new_length;
                                while (l < new_length) {
                                    elems[l++] = template.cloneNode(true)
                                };
                            }
                        )
                    ;
                    
                    push_text_utf8(
                        TD.decode(blocks[0]),
                        messages,

                        0,
                        messages.length,

                        elements,
                        0,
                        alloc_state.length_loaded_elements,

                        messages_fragment,
                        messages_range,
                        list,
                        MESSAGE_ROW_EL,

                        expand_nodes,
                        render_element,

                        alloc_state,
                        style_state,
                    );
                    
                    on_window_resize(window_event_object);
                    
                    // (c_av.background = 'url("/f/image/png/logo_full30.png")'),
                    // (chatbar_h1.textContent = "Enter password"),
                    
                    return body_cl.add('a');
                }
            )
        );
    }
)(
    window,
    document,
    globalThis.Math,
    globalThis.FontFace,
);
