
// message = {
//     chat_id: 8,
//     id: 8,

//     block_id: 4,
//     offset: 2,
//     length: 2,

//     timestamp: 8,
// };

export default (
    (
        string,
        messages,

        i,
        l,

        elements,
        elements_i,
        elements_l,

        fragment,
        range,
        list,
        template,

        expand_nodes,
        render_element,

        alloc_state,
        style_state,
    ) => {
        var
            element = null,
            inline = null,

            message = null,

            offset = 0,
            length = 0,

            char_i = 0,
            char_l = 0,

            top = 0,
            bottom = 0,

            elements_l_start = elements_l,

            row_height = style_state.row_height,
            height = style_state.loaded_height,

            string_offset = 0,
            rects = null,
            rect = null
        ;
        general: {
            append: {
                while (i < l) {
                    message = messages[i];

                    string_offset = char_i = message.offset;
                    char_l = (string_offset + (message.length));
                    
                    while (char_i < char_l) {
                        if (string[char_i] === "\n") {
                            if (elements_i < elements_l) {
                                render_element(
                                    elements[elements_i++],
                                    string,

                                    string_offset,
                                    (string_offset = char_i),
                                );
                            }
                            else {
                                break append;
                            };
                        };
                        char_i++;
                    };

                    if ((char_i < char_l) && (elements_i < elements_l)) {
                        render_element(
                            elements[elements_i++],
                            string,

                            string_offset,
                            (string_offset = char_i),
                        );
                    }
                    else {
                        break append;
                    };

                    i++;
                }
                break general;
            };

            expand_nodes(
                elements,
                elements_i,
                elements_l,
                template,
                alloc_state
            );
            
            render_element(
                (element = elements[elements_i++]),
                string,

                string_offset,
                (string_offset = (char_i)),
            );

            elements_l++;
            element.style.top = `${height}px`;
            height += row_height;
            
            fragment.appendChild(element);

            while (char_i < char_l) {
                if (string[char_i] === "\n") {
                    render_element(
                        (element = elements[elements_i++]),
                        string,

                        string_offset,
                        (string_offset = char_i),
                    );

                    elements_l++;
                    element.style.top = `${height}px`;
                    height += row_height;

                    fragment.appendChild(element);
                };
                char_i++;
            };

            if (char_i < char_l) {
                render_element(
                    (element = elements[elements_i++]),
                    string,

                    string_offset,
                    (string_offset = char_i),
                );

                elements_l++;
                element.style.top = `${height}px`;
                height += row_height;

                fragment.appendChild(element);
            };

            while (i < l) {
                message = messages[i];

                string_offset = char_i = message.offset;
                char_l = (string_offset + (message.length));
                
                while (char_i < char_l) {
                    if (string[char_i] === "\n") {
                        render_element(
                            (element = elements[elements_i++]),
                            string,
        
                            string_offset,
                            (string_offset = char_i),
                        );
        
                        elements_l++;
                        element.style.top = `${height}px`;
                        height += row_height;
        
                        fragment.appendChild(element);
                    };
                    char_i++
                };

                if (char_i < char_l) {
                    render_element(
                        elements[elements_i++],
                        string,

                        string_offset,
                        (string_offset = char_i),
                    );
                };

                i++;
            }
        };

        if (elements_l > elements_l_start) {
            list.appendChild(fragment);
            alloc_state.length_loaded_elements = elements_l;
            style_state.loaded_height = height;
        }
        else if (elements_i < elements_l) {
            range.setStartBefore(elements[elements_i]);
            range.setEndAfter(elements[elements_l - 1]);

            rects = range.getClientRects();

            i = 0;
            l = rects.length;

            while(i < l){
                if ((rect = rects[i++]).height) {
                    (r.top < top) && (top = r.top);
                    (r.bottom > bottom) && (bottom = r.bottom);
                };
            };

            range.deleteContents();
            
            alloc_state.length_loaded_elements = (elements_i);
            style_state.loaded_height = (height - (bottom - top))
        };
        
        return undefined;
    }
);
